<template>
  <div id="About">
    <el-container>
      <el-header>
        <div class="box">
          <img src="https://www.escook.cn/vue-shop/img/heima.b5a855ee.png" alt />
          <h3>电商后台管理系统</h3>
          <p>
            <el-button type="danger" @click="goOut">退出</el-button>
          </p>
        </div>
      </el-header>
      <el-container>
        <el-menu
          default-active="2"
          class="el-menu-vertical-demo,el-aside"
          @open="handleOpen"
          @close="handleClose"
          :collapse="isCollapse"
          background-color="#333744"
          text-color="#fff"
          :collapse-transition="false"
          :style="{width:(isCollapse?'64px':'200px')}"
          unique-opened
        >
          <div class="hk" @click="go">|||</div>
          <el-submenu v-for="(i,k) in list" :index="i.id.toString()" :key="i.id">
            <template slot="title">
              <i :class="icon[k]"></i>
              <span slot="title">{{i.authName}}</span>
            </template>
            <el-menu-item-group class="elel" v-for="(ie,ke) in i.children" :key="ke">
              <el-menu-item :index="ie.id.toString()" @click="toxq(ie.path,i.authName,ie.authName)">
                <i class="el-icon-setting"></i>
                {{ie.authName}}
              </el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "About",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      token: sessionStorage.getItem("token"),
      isCollapse: false,
      list: [],
      icon: [
        "el-icon-s-custom",
        "el-icon-s-grid",
        "el-icon-s-shop",
        "el-icon-s-claim",
        "el-icon-s-platform"
      ]
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    goOut() {
      (this.token = []), this.$router.push("/");
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    go() {
      this.isCollapse = !this.isCollapse;
      console.log(this.isCollapse);
    },
    toxq(k, c, t) {
      sessionStorage.setItem("ly", k);
      sessionStorage.setItem("yi", c);
      sessionStorage.setItem("er", t);
      this.$router.push(k);
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
 /**
 * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
 * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
 */
  mounted() {
    this.axios.get("menus").then(res => {
      // console.log(res.data.data);
      this.list = res.data.data;
    });
  },
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {}
};
</script> 

